.faqs {
	.container {
		position: relative;
	}
	.container-sm {
		position: relative;
		z-index: 2;
	}
	&__img-group {
		display: inline-block;
		position: absolute;
		top: -39px;
		right: 62px;
		height: 100%;
		@include mediaMax($xl) {
			right: 150px;
		}
		@include mediaMax($lg) {
			display: none;
		}
		img {
			&:nth-child(1) {
				position: absolute;
				bottom: -600px;
				right: -200px;
				&._anim-show {
					transform: translate(0, -160%);
					transition: all 2.8s ease;
				}
				@include mediaMax($lg) {
					top: -200px;
					&._anim-show {
						transform: translate(0, -20%);
						transition: all 2.8s ease;
					}
				}
				@include mediaMax($md) {
					top: -305px;
					left: 10px;
				}
			}
			&:nth-child(2) {
				position: absolute;
				top: 170px;
				right: -30px;
				max-width: 48px;
				z-index: 2;
				&._anim-show {
					transform: translate(0, -200%);
					transition: all 3.8s ease;
					@include mediaMax($lg) {
						display: none;
					}
				}
			}
			&:nth-child(3) {
				position: absolute;
				top: 500px;
				right: -20px;
				z-index: 3;
				max-width: 30px;
				&._anim-show {
					transform: translate(0, -150%);

					transition: all 2.5s ease;
				}
				@include mediaMax($lg) {
					bottom: 133px;
					right: 20px;
					&._anim-show {
						transform: translate(0, -100%);

						transition: all 2.5s ease;
					}
				}
			}
			&:nth-child(4) {
				display: none;
			}
			&:nth-child(5) {
				&._anim-show {
					transform: translate(0, -100%);
					transition: all 2s ease;
				}
			}
			&:nth-child(6) {
				min-width: 480px;
				min-height: 480px;
				position: absolute;
				top: 50px;
				right: -500px;
				&._anim-show {
					transform: translate(0, -20%);
					transition: all 2.8s ease;
				}
				@include mediaMax($lg) {
					display: none;
				}
			}
		}
	}
}
